import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Home from "./Home";
import Truth from "./Truth";
import './styles.css';
import breloom from "./breloom.png";
import github from "./github.png";
import PageNotFound from "./404";

function App() {
  return (
    <div class="page">
      <a href="https://breloom.xyz/"><img src={breloom} id="home" /></a> <br />
      <a href="https://github.com/BreloomBoom"><img src={github} id="github" /></a>
      <Router>
        <div>
          <Switch>
            <Route exact path="/truthtable">
              <Truth />
            </Route>
            <Route exact path="/">
              <Home />
            </Route>
            <Route component={PageNotFound} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;